svg {
  width: 32px;
  height: 32px;
}

@media only screen and (max-width: 768px) {
  svg {
    display: none;
  }
}
/*# sourceMappingURL=Icons.css.map */
nav {
  height: 12vh;
  width: 100vw;
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: #d2e0bf;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}

nav .title {
  letter-spacing: 3px;
  font-weight: 500;
  font-size: 1.6vw;
}

nav .tutorial-button {
  background: none;
  border: none;
  position: absolute;
  right: 5vw;
  text-shadow: none;
  outline: none;
  transition: 0.5s ease-in-out;
  height: 4vh;
}

nav .tutorial-button h5 {
  font-size: 20px;
  position: absolute;
  right: 0px;
  top: -30px;
}

nav button:hover {
  transform: rotate(360deg);
}

@media only screen and (max-width: 1024px) {
  nav .title {
    font-size: 2.5vw;
  }
}

@media only screen and (max-width: 768px) {
  nav .title {
    font-size: 3vw;
  }
}

@media only screen and (min-width: 769px) {
  nav .tutorial-button h5 {
    display: none;
  }
}

@media only screen and (max-width: 600px) {
  nav .title {
    font-size: 5vw;
  }
}

@media only screen and (max-width: 330px) {
  nav .title {
    font-size: 5.5vw;
  }
}

@media only screen and (max-width: 800px) {
  nav .tutorial-button {
    transition: none;
  }
}
